@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;500;700&family=Roboto&display=swap);
* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.nav-bar {
  position: fixed;
  background-color: white;
  top: 0;
  width: 100vw;
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
  font-size: 24px;
  display: flex;
  align-items: center;
  justify-content: space-around;
  border-bottom: 1px solid black;
  height: 100px;
  top: 0;
  z-index: 2;
}

.title a {
  font-size: 40px;
  text-decoration: none;
  text-transform: uppercase;
  color: inherit;
}

.nav-list {
  width: 20%;
  display: flex;
  justify-content: space-around;
  list-style-type: none;
}

.nav-list a {
  text-decoration: none;
  text-transform: uppercase;
  color: inherit;
  font-size: 24px;
}

.cart-counter {
    display: flex;
    align-items: center;
}

.cart-button {
    background-color: white;
    border: none;
    font-size: 16px;
}


@media only screen and (max-width: 1366px) {
  .nav-list a {
    font-size: 20px;
  }

  .cart-button-text {
    font-size: 14px;
  }
}

@media only screen and (max-width: 1024px) {

  .title a {
    font-size: 30px;
  }

  .nav-list a {
    font-size: 16px;
  }
}

@media only screen and (max-width: 500px) {

  .nav-list a {
    display: none;
  }
}

.cart-button {
    display: flex;
    align-items: center;
}

.cart-button:hover {
    cursor: pointer;
}

.cart-button-text {
    text-transform: uppercase;
    padding-top: 5px;
    margin-right: 5px;
}

.cart-counter {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 5px;
    width: 36px;
    height: 36px;
    background-image: url(data:image/png;base64,);
    background-repeat: no-repeat;
    background-size: 36px;
    color: white;
}
.home-container {
  width: 90%;
  height: 100%;
  padding-top: 40px;
  justify-content: center;
}



.flex-row {
  display: flex;
  justify-content: center;
  width: 100%;
  height: auto;
  flex-wrap: wrap;
}

.hero-container, .form-container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  position: relative;
  margin: 1.5%;
}

.hero-image {
  width: 100%;
}

.hero-container-watches {
  width: 52.5%;
}

.hero-container-chrono {
  width: 35%;
}

.hero-container-wallet {
  width: 35%;
}


.hero-container-sunglasses {
  width: 52.5%;
}


.button-container {
  display: flex;
  position: absolute;
  bottom: 10%;
  background-color: white;
  color: inherit;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.tag-line {
  font-size: 32px;
  padding: 0.5em 2em;
  margin: 0;
  letter-spacing: 0.05em;
}

a {
  font-size: 18px;
  display: block;
  padding: 0.75em 2em;
}

.shop-now-button {
  background-color: black;
  color: white;
  font-family: 'Roboto', sans-serif;
  border: none;
  margin-bottom: 20px;
}

.shop-now-button a {
  text-decoration: none;
  color: inherit;
}

.shop-now-button:hover {
  cursor: pointer;
}

@media only screen and (max-width: 1566px) {
  .tag-line {
    font-size: 26px;
  }

  a {
    font-size: 14px;
  }
}

@media only screen and (max-width: 1365px) {
  .tag-line {
    font-size: 20px;
    padding: 0.75em 3em;
  }

  a {
    font-size: 14px;
  }

  .hero-container {
    margin: 2.5%;
  }

  .hero-container-watches {
    width: 100%;
  }
  .hero-container-chrono {
    width: 45%;
  }
  .hero-container-wallet {
    width: 45%;
  }
  .hero-container-sunglasses {
    width: 100%;
  }
}

@media only screen and (max-width: 1023px) {
  .tag-line {
    font-size: 14px;
    padding: 0.5em 2em;
  }
  a {
    font-size: 12px;
  }
}

@media only screen and (max-width: 700px ) {
  .tag-line {
    font-size: 14px;
    padding: 0.5em 1em;
  }

  a {
    padding: 0.5em 1em;
  }
}

@media only screen and (max-width: 414px) {
  .tag-line {
    font-size: 14px;
    padding: 0.5em 1em;
  }

  .hero-container-watches {
    width: 100%;
  }
  .hero-container-chrono {
    width: 100%;
  }
  .hero-container-wallet {
    width: 100%;
  }
  .hero-container-sunglasses {
    width: 100%;
  }
}

.product-card-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 25%;
    height: 55vh;
}

.product-product-box {
    transition: -webkit-transform 0.2s ease-out;
    transition: transform 0.2s ease-out;
    transition: transform 0.2s ease-out, -webkit-transform 0.2s ease-out;
}

.focus>.product-product-box {
    -webkit-transform: translateY(-40px);
            transform: translateY(-40px);
}

/* .product-card-container:hover>.add-to-cart-card {
    display: block;
} */

.product-card-image {
    width: 100%;
    z-index: -3;
}

.product-card-name {
    font-size: 12px;
    color: #888;
    margin: 5px;
    text-transform: uppercase;
}

.product-card-color {
    font-family: 'Montserrat', sans-serif;
    font-weight: 700;
    font-size: 16px;
    text-transform: uppercase;
    letter-spacing: 0.075em;
    margin: 5px;
}

.product-card-price {
    font-family: 'Montserrat', sans-serif;
    font-weight: 500;
    font-size: 14px;
    margin: 5px;
    letter-spacing: 0.1em;
}

.product-product-info {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0;
}

.add-to-cart-card {
    font-size: 18px;
    font-family: 'Montserrat', sans-serif;
    font-weight: 700;
    text-transform: uppercase;
    background-color: white;
    color: black;
    border: 2px solid black;
    margin: 0;
    padding: 15px 40px;
    width: 80%;
    position: relative;
    bottom: 30px;
    transition: opacity 0.1s linear 0.05s;
}

.add-to-cart-card:hover {
    cursor: pointer;
    color: white;
    background-color: black;
}

.hidden {
    visibility: collapse;
    opacity: 0;
}


.focus>.add-to-cart-card {
    visibility: visible;
    opacity: 1;
}

@media only screen and (max-width: 1365px) {
    .product-card-container {
        width: 33%;
        height: auto;
    }

    .product-product-box {
        -webkit-transform: translateY(-40px);
                transform: translateY(-40px);
    }

    .add-to-cart-card {
        visibility: visible;
        opacity: 1;
        color: white;
        background-color: black;
        font-size: 14px;
        margin-bottom: 10px;
    }
}

@media only screen and (max-width: 1023px) {
    .product-card-container {
        width: 50%;
        height: auto;
    }

    .product-card-color {
        font-size: 12px;
    }

    .add-to-cart-card {
        font-size: 12px;
        padding: 10px 20px;
    }
}

@media only screen and (max-width: 414px) {
    .product-card-container {
        width: 100%;
    }
}
.shop-container {
    display: flex;
    align-items: center;
    justify-content: center;
