    min-height: calc(100vh - 100px);
}

.filter-container {
    opacity: 0;
    position: absolute;
    top: 0;
    background-color: black;
    -webkit-filter: brightness(30%);
            filter: brightness(30%);
    width: 100%;
    height: 100%;
    z-index: -2;
    transition: opacity 0.2s ease-out 0.1s;
}

.main-layout-checkout>.filter-container{
    visibility: visible;
    opacity: 0.75;
    z-index: 1;
}

.main-layout-checkout>.shopping-cart-container {
    -webkit-transform: translateX(-25vw);
            transform: translateX(-25vw);
}

